html {
  font-size: $text-size;
}
body {
  font-size: 100%;
  font-family: $font-family;
  font-weight: $font-weight;
  line-height: (20/16);
}

//normalize 
.ul-normalize {
  padding-left: 0;
  list-style: none;
}

//title and text
.h2{
  text-transform: uppercase;
  font-size: (24rem/16);
  font-weight: 400;
  line-height: (34/24);
  text-align: center;
  letter-spacing: 0.1em;
  margin-bottom: $text-size;
  @include media-breakpoint-up('lg') {
    font-size: (28rem/16);
  }
}
.h3 {
  font-size: (24rem/16);
  font-weight: 400;
  line-height: (29/24);
  margin-bottom: $text-size;
}

.text-grey {
  color: $color-text-grey;
}
.text-transparent {
  color: transparent;
}
.text-center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

p {
  max-width: 55.375em;
  margin-bottom: $text-size;
}

ul, ol {
  margin-bottom: $text-size;
}

ul, ol, li, p, h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  &:last-child:not(.save-margin) {
    margin-bottom: 0;
  }
} 

//list-mod
.list-icons {
  li {
   position: relative;
   padding-left: 28px;
  }

  li + li {
    margin-top: 9px;
  }

  &__icon {
    position: absolute;
    top: 50%;
    width: auto;
    height: auto;
    max-height: 19px;
    max-width: 25px;
    right: calc(100% - 9.5px);
    transform: translate(50%, -50%);
  }
}

//margin 
.half-text-margin {
  margin-bottom: $text-size * 0.5;
}


//links
a {
  text-decoration: none;
  border-bottom: 1px solid;
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
    border-color: transparent;
  }
}

a.like-text {
  color: inherit;
  transition: opacity $transition;
  border-bottom: 0;
  &:hover {
    color: inherit;
    opacity: 0.6;
  }
}

//button
.btn {
  border-radius: (10em/16);
  font-size: $text-size;
  padding: (18em/16) (30em/16);
  font-weight: 700;
  min-width: 100px;
  text-align: center;
}

a.btn {
  display: inline-block;
  border-bottom: 0;
}

.btn--full {
  background-color: $color-accent;
  background-image: $gradient-accent;
  box-shadow: inset 0px 0px 0px rgba(0, 63, 109, 0.6);
  color: $color-text;
  transition: box-shadow $transition;

  &:hover {
    color: $color-text;
    box-shadow: inset 0px 4px 4px rgba(0, 63, 109, 0.6);
  }
}

.btn--line {
  background: none;
  box-shadow: inset 0 0 0 1px $color-text-grey;
  color: $color-text-grey;
  transition: color $transition, box-shadow $transition;
  
  &:hover {
    color: $color-text;
    box-shadow: inset 0 0 0 1px $color-text;
  }
}